import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

export default class CarouselAniversario extends Component {

      render() {
        const getConfigurableProps = () => ({
          showArrows:  false,
          showStatus: false,
          showIndicators: true,
          infiniteLoop: true,
          showThumbs: false,
          useKeyboardArrows: true,
          autoPlay: true,
          stopOnHover: true,
          swipeable: true,
          dynamicHeight: true,
          emulateTouch: true,
          //thumbWidth: number('thumbWidth', 100, {}),
          //selectedItem: number('selectedItem', 0, {}),
          interval: 6000,
          transitionTime: 600,
          swipeScrollTolerance: 5
      });
        return (
          <>
          { this.props.images.length > 0 ? (
          <Carousel id='carousel-imagem' {...getConfigurableProps()}>
            {this.props.images.map(item=>{ return (
             <div key={item.image}>
               <img src={item.image} />
               <p className="legend" style={{'opacity': 1}}>{item.legend}</p>
             </div>
               )
             })}
          </Carousel>
          ): (<></>)
        }
        </>)
    }
}
