import { createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const exerciciosSlice = createSlice({
  name: 'exercicios',
  initialState: {
    exercicios: [],
    exercicios_filtrados: [],
    gruposMusculares: []
  },
  reducers: {
    getExercicios: (state, action) => {
      console.log(action)
      state.exercicios = [...action.payload];
      state.exercicios_filtrados = [...action.payload];
    },
    getGruposMusculares: (state, action) =>{
      state.gruposMusculares = [...action.payload]
    },
    filtrarExercicios: (state, action) => {
      console.log(action.payload)
      if(action.payload.query != ''){
        state.exercicios_filtrados = [...state.exercicios.filter(item=>{
          let name_match = item.name.toLowerCase().includes(action.payload.query.toLowerCase());

          return name_match;

        })]

      }else{
        state.exercicios_filtrados = [...state.exercicios];
      }
    },

  },
});

const { getExercicios,  getGruposMusculares, filtrarExercicios} = exerciciosSlice.actions;

export const filtrar_exercicios = (dispatch, data='') =>{
  dispatch( filtrarExercicios({ query: data }) )
}
export const recuperarExercicios = (dispatch) => {
  axios.get('https://186.240.37.214:8000/api/v1/exercicios/').then(res =>{
      console.log(res)
        dispatch(getExercicios( res.data  ));
  })

};
export const recuperarGruposMusculares = (dispatch) => {
  axios.get('https://186.240.37.214:8000/api/v1/grupos-musculares/').then( res=>{
      dispatch(getGruposMusculares(  res.data  ));
    });
};

export default exerciciosSlice.reducer;
