import { createSlice } from '@reduxjs/toolkit';
import axios from 'axios';

export const aniversariantesSlice = createSlice({
  name: 'aniversariantes',
  initialState: {
    aniversariantes: [],

  },
  reducers: {
    getaniversariantes: (state, action) => {
      console.log(action)
      state.aniversariantes = [...action.payload];
    },
  },
});

const { getaniversariantes  } = aniversariantesSlice.actions;
console.log(aniversariantesSlice.actions)
export const recuperarAniversariantes = (dispatch, data='') =>{
  axios.get('https://186.240.37.214:8000/api/v1/aniversariante/').then(res =>{
        dispatch(getaniversariantes(  res.data  ));
  })
}


export default aniversariantesSlice.reducer;
