
import React, {useState, useEffect} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import Carousel from '../../components/Carousel'
import './style.css';

import {
  recuperarGruposMusculares,
  recuperarExercicios,
  filtrar_exercicios
} from '../../stores/exercicios/exercicioSlice';
import {
  recuperarAniversariantes,
} from '../../stores/aniversariantes/aniversariantesSlice';

import store from '../../stores/store';

function Home() {
    const dispatch = useDispatch();
    const [show, setShow] = useState(false);
    const [img, setImg] = useState(false);
    const [exercicioSelecionado, setexercicioSelecionado] = useState({ image: '',description:''});
    const handleClose = () => setShow(false);
    const handleShow = (id) =>{
      let selecionado = exercicios.find( item => item.id == id)
      setexercicioSelecionado(selecionado)

      setImg(!img);
      setShow(true);
    };
    const exercicios = useSelector(state => state.exercicio.exercicios_filtrados );
    const gruposMusculares = useSelector(state => state.exercicio.gruposMusculares);
    const aniversariantes = useSelector(state => state.aniversariantes.aniversariantes);

    useEffect(() => {
      recuperarGruposMusculares(dispatch);
      recuperarExercicios(dispatch);
      recuperarAniversariantes(dispatch);
    }, [])

    return (
      <>
      <div className="container-fluid h-100">
        <div className="row">
        </div>
        <div className="row corpo">

          <div className="col-md-12  d-md-inline-block ">
            <div className="row">
                <div className="col-md-12  d-md-inline-block p-0">
                <div className="d-flex flex-column justify-content-center color-scheme align-items-center" style={{'height': '8vh','marginBottom': '2vh'}}>
                <img className="logo" src="logo.png" />
                </div>
              </div>
              <div className="col-md-10 offset-md-1 d-md-inline-block " style={{'height': '5vh'}}>
                <div className="d-flex flex-column justify-content-start">
                  <input  type="text" placeholder="Digite o nome do exerc√≠cio ou grupo muscular"
                    onChange={(e)=>{
                      filtrar_exercicios(dispatch,e.target.value)
                    }}
                   />
                 </div>
               </div>
            </div>
            <div className="row">
            <div className="col-md-5  offset-md-1 d-md-inline-block ">
              <div className="d-flex flex-column justify-content-start exercicio-container">
            { exercicios.map(item=>(
              <div key={item.id} className="col-md-12 mb-2 text-white bg-dark exercicio-item" onClick={()=>{
                  handleShow(item.id)
                }
              }>
                <h3 style={{'textAlign': 'left', 'textTransform': 'uppercase'}}>{item.name}</h3>
              </div>
              ))
            }

              <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton >
                  <Modal.Title>{exercicioSelecionado.name}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <div className="d-flex flex-row">
                    <div>
                      {
                        exercicioSelecionado.video  == null ?
                        <img className="modal-image" src={exercicioSelecionado.image} /> :
                        <video src={exercicioSelecionado.video} type="video/mp4" controls/>
                      }
                    </div>
                    <div className="ml-4">
                      {exercicioSelecionado.description}
                    </div>
                  </div>
                </Modal.Body>
                <Modal.Footer >
                  <Button variant="secondary" onClick={handleClose} >
                    Fechar
                  </Button>
                </Modal.Footer>
              </Modal>
            </div>
          </div>
          <div className="col-md-5  offset-md-0 d-md-inline-block ">
            <div className="d-flex flex-column justify-content-start">
            <Carousel images={aniversariantes}/>
            </div>
          </div>
          </div>

          <div className="row">
            <div className="col-md-12  d-md-inline-block p-0 " style={{'height': '5vh'}}>
              <div className="d-flex flex-column justify-content-end " style={{'height': '100%'}}>
                <h5 className="text-center color-scheme mb-0">Profit Academia - 2020</h5>
              </div>
            </div>
          </div>

          </div>
        </div>
      </div>
      </>

    );
}

export default Home;
